import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import '../api/api'

Vue.use(Vuex) 
export default new Vuex.Store({  
    //定义初始数据
    state: {  
        title: '',
        adminUserList: [],
        isShow: false,
        token:window.sessionStorage.getItem('token')
    },
    getters: {
        token: state=>state.token
    },
})


