import Navigator from '../components/navigator/navigator.vue'

let RouterTable = {
	mode: 'history',
	routes: [
	{
		path: '/',
		name: 'Home',
		components: {
			default: () =>
			import ('@/modules/Home/Home.vue'),
			header: Navigator
		},
		redirect:'/Home',
        meta: {
            requireAuth: true
        }
	},
    {
        path: '/Login',
        name: 'Login',
        components: {
            default: () =>
                import ('@/modules/login/login.vue'),
        },
        meta: {
            requireAuth: false
        }
    },
	{
		path: '/Home',
        name: 'Home',
        components: {
            default: () =>
                import ('@/modules/Home/Home.vue'),
            header: Navigator
        },
        meta: {
            requireAuth: true
        }
    },
    {
    	path: '/User',
        name: 'User',
        components: {
            default: () =>
                import ('@/modules/User/User.vue'),
            header: Navigator
        }
    },
    {
        path: '/BanZ',
        name: 'BanZ',
        components: {
            default: () =>
                import ('@/modules/BanZ/BanZ.vue'),
            header: Navigator
        } 
    },{
        path: '/Photo',
        name: 'Photo',
        components: {
            default: () =>
                import ('@/modules/Photo/Photo.vue'),
            header: Navigator
        }
    },{
        path: '/PassWord',
        name: 'PassWord',
        components: {
            default: () =>
                import ('@/modules/PassWord/PassWord.vue'),
            header: Navigator
        }
    },{
        path: '/Books',
        name: 'Books',
        components: {
            default: () =>
                import ('@/modules/Books/Books.vue'),
            header: Navigator
        }
    },{
        path: '/Wish',
        name: 'Wish',
        components: {
            default: () =>
                import ('@/modules/Wish/Wish.vue'),
            header: Navigator
        }
    },,{
        path: '/Share',
        name: 'Share',
        components: {
            default: () =>
                import ('@/modules/Share/Share.vue'),
            header: Navigator
        }
    }
	]
}

export default RouterTable


