import Vue from 'vue';
export default{
	data(){
		return{
			nav:[
			{
				title:'家庭式成员',
				path:'/User'
			},
			{
				title:'搬砖日常',
				path:'/BanZ'
			},
			{
				title:'相册',
				path:'/Photo'
			},
			{
				title:'密码本',
				path:'/PassWord'
			},
			{
				title:'账本',
				path:'/Books'
			},
			{
				title:'心愿',
				path:'/Wish'
			},
			{
				title:'分享内容',
				path:'/Share'
			}],
			changeBg:-1
		}
	},
	methods:{
		turnHome(){
			this.$router.push({name:'Home'});
		}
	}
}


